@import url(https://fonts.googleapis.com/css2?family=Bonheur+Royale&family=Source+Sans+Pro:wght@200;300;400;600&display=swap);
.main-container {
  width: 50vw;
  min-height: 60vh;
  margin: 0 auto;
  border: 3px solid gray;
  border-radius: 10px;
}
.heading {
  /* color: #fff; */
  font-family: "Source Sans Pro", sans-serif;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 2rem;
}
.register {
  float: right;
}
.inputs-wrapper {
  text-align: center;
}
.inputs-wrapper input {
  padding: 0.3rem 0.7rem;
  margin-bottom: 1rem;
  margin-right: 1rem;
  border: none;
  border-bottom: 2px solid gray;
  outline: none;
}
.icons {
  font-size: 1.2rem;
  cursor: pointer;
}
.add-icon {
  font-size: 2rem;
  color: rgb(67, 250, 11);
}
.delete-icon {
  color: rgb(231, 25, 25);
}
li {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.5rem;
  text-transform: capitalize;
  margin-bottom: 0.5rem;
}
li::marker {
  color: blue;
  margin-right: 1rem;
}
.todos {
  display: flex;
  justify-content: space-between;
}
.icons-wrapper {
  width: 10rem;
  display: flex;
  justify-content: space-evenly;
}
/* responsive code  */
@media (max-width: 780px) {
  .main-container {
    width: 100%;
  }
}

nav {
  width: 100%;
  height: 3rem;
  background-color: rgb(57, 57, 214);
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
nav .nav-link {
  color: white;
  font-size: 1.2rem;
  text-decoration: none;
  margin-right: 1rem;
  letter-spacing: 0.1rem;
}
nav .nav-link:hover {
  color: rgb(218, 202, 202);
}

.registration-main-div {
  /* background: rgb(170, 142, 142); */
  width: 100%;
}
.form {
  margin: 0 auto;
  width: 30rem;
  border: 2px solid gray;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  padding: 2rem;
  border-radius: 1rem;
}
.form input {
  border: none;
  outline: none;
  width: 100%;
  padding: 0.4rem 0.8rem;
  font-size: 1rem;
  border-bottom: 0.2px solid gray;
}
.control-label {
  font-size: 0.9rem;
  padding-bottom: 0.7rem;
}
.submit-button {
  padding: 0.8rem;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  outline: none;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.submit-button:hover {
  background-color: rgb(105, 105, 226);
}
.input-wrapper {
  display: flex;
  align-items: center;
}
.input-wrapper i {
  margin-right: 1rem;
  font-size: 1.3rem;
}
.heading {
  margin-bottom: 0;
  text-decoration: underline;
}
p {
  color: red;
}
/* responsive */
@media (max-width: 880px) {
  .form {
    max-width: 100vw;
    font-size: 8px;
    justify-content: center;
  }
  .form input {
    font-size: 10px;
    width: none;
  }
}

